import { gsap, Power2 } from 'gsap'
export default {
  props: {
    media: {
      type: Object,
      default: null,
    },
  },

  data() {
    return {}
  },
  mounted() {
    if (window) {
      window.dispatchEvent(new Event('resize'))
    }
    this.$Lazyload.$on(
      'loaded',
      (
        {
          bindType,
          el,
          naturalHeight,
          naturalWidth,
          $parent,
          src,
          loading,
          error,
        },
        formCache
      ) => {
        if (window) {
          if (el.classList.contains('media-full')) {
            gsap.to(el, {
              // clipPath: 'inset(0% 0% 0% 0%)',
              // webkitClipPath: 'inset(0% 0% 0% 0%)',
              scaleY: 1,
              autoAlpha: 1,
              duration: 0.5,
              ease: Power2.easeInOut,
              onStart: () => {
                window.dispatchEvent(new Event('resize'))
              },
            })
          }
        }
      }
    )
  },
  methods: {
    loaded() {
      if (window) {
        window.dispatchEvent(new Event('resize'))
      }
    },
  },
}
